:root {
  --bg: #f1f8f4;
  --panel: #ffffff;
  --text: #1f2937;
  --muted: #6b7280;
  --brand: #059669;
  --brand-700: #047857;
  --danger: #ef4444;
  --border: #e5e7eb;
  --shadow: 0 10px 30px rgba(0,0,0,.06);
}

/* Layout */
* { box-sizing: border-box; }
html, body, #root { height: 100%; }
body { margin: 0; font-family: system-ui, -apple-system, Segoe UI, Roboto, sans-serif; color: var(--text); background: var(--bg); }
.page { min-height: 100vh; }
.container { max-width: 1100px; margin: 0 auto; padding: 0 20px; }

/* Header */
.header { position: sticky; top: 0; background: rgba(255,255,255,.9); backdrop-filter: blur(8px); border-bottom: 1px solid var(--border); z-index: 20; }
.header__inner { display: flex; align-items: center; justify-content: space-between; padding: 12px 0; }
.brand { display: inline-flex; gap: 8px; align-items: center; font-weight: 700; text-decoration: none; color: inherit; }
.nav { display: flex; gap: 18px; align-items: center; }
.nav__link { text-decoration: none; color: inherit; position: relative; }
.nav__link:hover { text-decoration: underline; }
.cartlink { display: inline-flex; gap: 6px; align-items: center; }
.badge { position: absolute; top: -10px; right: -12px; background: var(--brand); color: #fff; padding: 2px 6px; border-radius: 999px; font-size: 11px; }

/* Hero */
.hero { height: 70vh; background-size: cover; background-position: center; }
.hero__shade { background: rgba(0,0,0,.45); height: 100%; }
.hero__content { height: 70vh; display: grid; align-content: center; max-width: 720px; color: #fff; }
.hero__title { font-size: clamp(32px, 4vw, 56px); margin: 0; line-height: 1.1; }
.hero__text { margin: 16px 0 28px; color: #ddf7ea; font-size: clamp(16px, 2vw, 20px); }

/* Buttons */
.btn { appearance: none; border: 1px solid var(--border); border-radius: 14px; padding: 10px 14px; background: #fff; cursor: pointer; }
.btn--primary { background: var(--brand); border-color: var(--brand); color: #fff; }
.btn--primary:hover { background: var(--brand-700); }
.btn--danger { border-color: var(--danger); color: var(--danger); background: #fff; }
.btn--full { width: 100%; }
.iconbtn { border: none; background: transparent; cursor: pointer; color: var(--muted); }
.iconbtn:hover { color: var(--danger); }

/* Sections */
.section { padding: 32px 0; }
.title { margin: 0 0 4px; font-size: 28px; }
.muted { color: var(--muted); }
.small { font-size: 12px; }
.row { display: flex; align-items: center; justify-content: space-between; margin-top: 8px; }
.bold { font-weight: 700; }
.vspace { height: 12px; }

/* Chips */
.chiprow { display: flex; flex-wrap: wrap; gap: 8px; margin-top: 14px; }
.chip { padding: 6px 12px; border: 1px solid var(--border); border-radius: 999px; background: #fff; cursor: pointer; }
.chip--active { background: var(--brand); border-color: var(--brand); color: #fff; }

/* Grid & Cards */
.grid { display: grid; grid-template-columns: repeat(auto-fill, minmax(260px, 1fr)); gap: 18px; margin-top: 18px; }
.grid-2 { display: grid; gap: 24px; grid-template-columns: 1fr; }
@media (min-width: 960px) { .grid-2 { grid-template-columns: 2fr 1fr; } }

.card { background: var(--panel); border-radius: 18px; overflow: hidden; box-shadow: var(--shadow); border: 1px solid var(--border); }
.card__img { width: 100%; height: 180px; object-fit: cover; display: block; }
.card__body { padding: 14px; }
.card__head { display: flex; align-items: start; justify-content: space-between; gap: 10px; }
.card__title { margin: 0; font-size: 18px; }
.price { font-weight: 700; }

/* Panels / Cart */
.panel { background: var(--panel); border: 1px solid var(--border); border-radius: 18px; padding: 18px; box-shadow: var(--shadow); }
.stack > * + * { margin-top: 12px; }

.cartrow { background: var(--panel); border: 1px solid var(--border); border-radius: 16px; padding: 12px; display: flex; gap: 12px; box-shadow: var(--shadow); }
.cartrow__img { width: 80px; height: 80px; object-fit: cover; border-radius: 12px; }
.cartrow__main { flex: 1; }
.cartrow__top { display: flex; align-items: center; justify-content: space-between; }
.cartrow__title { margin: 0; }
.cartrow__bottom { margin-top: 10px; display: flex; align-items: center; justify-content: space-between; }
.qty { display: inline-flex; align-items: center; gap: 10px; border: 1px solid var(--border); border-radius: 12px; padding: 4px 8px; }
.qty__btn { border: none; background: transparent; cursor: pointer; padding: 4px; }
.qty__val { width: 28px; text-align: center; }
.cartrow__sum { font-weight: 700; }
